/*--------------------------------------------------------------
# Embedded Fonts
--------------------------------------------------------------*/
@font-face {
  font-family: 'Exo';
  src: url("../../fonts/Exo-MediumItalic.woff2") format("woff2"), url("../../fonts/Exo-MediumItalic.woff") format("woff");
  font-weight: 500;
  font-style: italic; }

@font-face {
  font-family: 'Exo';
  src: url("../../fonts/Exo-Medium.woff2") format("woff2"), url("../../fonts/Exo-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal; }

@font-face {
  font-family: 'Exo';
  src: url("../../fonts/Exo-Light.woff2") format("woff2"), url("../../fonts/Exo-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal; }

@font-face {
  font-family: 'Exo';
  src: url("../../fonts/Exo-LightItalic.woff2") format("woff2"), url("../../fonts/Exo-LightItalic.woff") format("woff");
  font-weight: 300;
  font-style: italic; }

/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
textarea {
  color: #fff;
  font-family: "Exo", sans-serif;
  font-size: 1rem;
  font-weight: 300;
  line-height: 1.5; }

/* Headings */
h1, h2, h3, h4, h5, h6 {
  clear: both;
  font-weight: 500; }

h2 {
  font-size: 1.25rem; }
  .page-template-page-archives h2 {
    margin-top: 3rem;
    font-size: 1.5rem; }

/* Copy */
p {
  margin-bottom: 1.5rem; }
  .archive p, .blog p, .single p {
    font-size: 1.25rem; }

dfn, cite, em, i {
  font-style: italic; }

blockquote {
  margin: 0 1.5rem; }

address {
  margin: 0 0 1.5rem; }

pre {
  background: #eee;
  font-family: "Courier 10 Pitch", Courier, monospace;
  font-size: .9375rem;
  line-height: 1.6;
  margin-bottom: 1.6rem;
  max-width: 100%;
  overflow: auto;
  padding: 1.6rem; }

code, kbd, tt, var {
  font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
  font-size: .9375rem; }

abbr, acronym {
  border-bottom: 1px dotted #666;
  cursor: help; }

mark, ins {
  background: #fff9c0;
  text-decoration: none; }

big {
  font-size: 125%; }

.source {
  font-style: italic; }

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
  box-sizing: border-box;
  font-size: 1rem; }

*,
*:before,
*:after {
  /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit; }

body {
  background: #222;
  /* Fallback for when there is no custom background color defined. */ }

blockquote,
q {
  quotes: '' ''; }
  blockquote:before, blockquote:after,
  q:before,
  q:after {
    content: ''; }

hr {
  background-color: #fff;
  border: 0;
  height: 1px;
  margin-bottom: 1.5rem; }

ul,
ol {
  margin: 0 0 1.5rem 3rem; }

ul {
  list-style: disc; }

ol {
  list-style: decimal; }

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 1.5rem; }

dt {
  font-weight: 500; }

dd {
  margin: 0 1.5rem 1.5rem; }

img {
  display: block;
  height: auto;
  /* Make sure images are scaled correctly. */
  max-width: 100%;
  /* Adhere to container width. */ }

table {
  margin: 0 0 1.5rem;
  width: 100%; }

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
/* Buttons */
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  background-color: transparent;
  border: 2px solid #00cc00;
  border-radius: 3px;
  color: #00cc00;
  font-size: 1rem;
  font-weight: 500;
  padding: 0.5rem 2.25rem; }
  button:hover,
  input[type="button"]:hover,
  input[type="reset"]:hover,
  input[type="submit"]:hover {
    color: #009900;
    border-color: #009900; }
  button:active, button:focus,
  input[type="button"]:active,
  input[type="button"]:focus,
  input[type="reset"]:active,
  input[type="reset"]:focus,
  input[type="submit"]:active,
  input[type="submit"]:focus {
    color: #009900;
    border-color: #009900; }
  .page-template-page-submit button, .page-template-page-submit
  input[type="button"], .page-template-page-submit
  input[type="reset"], .page-template-page-submit
  input[type="submit"] {
    display: block; }

.search-icon {
  border: none solid transparent;
  padding-right: .5rem;
  padding-left: .5rem; }
  .search-icon i {
    color: #00cc00; }

/* Fields */
input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea {
  background-color: transparent;
  border: 2px solid #00cc00;
  border-radius: 3px;
  color: #00cc00;
  font-size: 1rem;
  font-weight: 500;
  padding: 0.5rem 0.5rem; }
  input[type="text"]:hover,
  input[type="email"]:hover,
  input[type="url"]:hover,
  input[type="password"]:hover,
  input[type="search"]:hover,
  textarea:hover {
    color: #009900;
    border-color: #009900; }
  input[type="text"]:active, input[type="text"]:focus,
  input[type="email"]:active,
  input[type="email"]:focus,
  input[type="url"]:active,
  input[type="url"]:focus,
  input[type="password"]:active,
  input[type="password"]:focus,
  input[type="search"]:active,
  input[type="search"]:focus,
  textarea:active,
  textarea:focus {
    color: #009900;
    border-color: #009900; }
  .page-template-page-submit input[type="text"], .page-template-page-submit
  input[type="email"], .page-template-page-submit
  input[type="url"], .page-template-page-submit
  input[type="password"], .page-template-page-submit
  input[type="search"], .page-template-page-submit
  textarea {
    display: block; }

textarea {
  width: 100%; }

@media only screen and (min-width: 620px) {
  input {
    width: 36%; }
    input[type="submit"] {
      width: auto; } }

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/* Links */
a {
  color: #00cc00;
  text-decoration: none; }
  a:visited {
    color: #00cc00; }
  a:hover, a:focus, a:active {
    color: #009900; }
  a:focus {
    outline: thin dotted; }
  a:hover, a:active {
    outline: 0; }

/* Lists */
ul, ol {
  list-style: none;
  margin: 0;
  padding: 0; }
  .page-template-page-archives ul, .page-template-page-archives ol {
    display: flex;
    flex-wrap: wrap; }
  ul li, ol li {
    margin-bottom: 0.75rem; }
    .page-template-page-archives ul li, .page-template-page-archives ol li {
      margin-right: 1.5rem; }

/* Menus */
.main-navigation {
  display: block;
  width: 100%;
  margin-bottom: 1rem; }
  @media only screen and (min-width: 620px) {
    .main-navigation {
      width: auto;
      padding-right: .8rem; } }
  .main-navigation ul {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: 0;
    padding-left: 0; }
    .main-navigation ul li {
      position: relative;
      margin: 0;
      padding: 0 .8rem; }
      .main-navigation ul li::after {
        position: absolute;
        content: '';
        top: 0;
        right: 0;
        width: 1px;
        height: 100%;
        border-right: 1px solid #fff; }
      .main-navigation ul li:nth-child(1) {
        padding-left: 0; }
      .main-navigation ul li:last-child {
        padding-right: 0; }
        .main-navigation ul li:last-child::after {
          display: none; }
        @media only screen and (min-width: 620px) {
          .main-navigation ul li:last-child {
            padding-right: .8rem; }
            .main-navigation ul li:last-child::after {
              display: block; } }
  .main-navigation a {
    display: block;
    text-decoration: none; }

/* Pagination */
.nav-links > *, .number-pagination > * {
  padding: 0 .25rem; }
  .nav-links > *:first-child, .number-pagination > *:first-child {
    padding-left: 0; }

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden; }
  .screen-reader-text:focus {
    background-color: #222;
    border-radius: 3px;
    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
    clip: auto !important;
    color: #21759b;
    display: block;
    font-size: .875rem;
    font-weight: 500;
    height: auto;
    left: 5px;
    line-height: normal;
    padding: 15px 23px 14px;
    text-decoration: none;
    top: 5px;
    width: auto;
    z-index: 100000;
    /* Above WP toolbar. */ }

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
  display: inline;
  float: left;
  margin-right: 1.5rem; }

.alignright {
  display: inline;
  float: right;
  margin-left: 1.5rem; }

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto; }

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clearfix {
  clear: both;
  content: "";
  display: table; }

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/* Posts and pages */
.sticky {
  display: block; }

.hentry {
  margin: 0 0 1.5rem; }
  @media only screen and (min-width: 620px) {
    .hentry {
      margin-bottom: 3rem; } }

.updated:not(.published) {
  display: none; }

.single .byline,
.group-blog .byline {
  display: inline; }

.page-content,
.entry-content,
.entry-summary {
  margin: 1.5rem 0 0; }

.page-links {
  clear: both;
  margin: 0 0 1.5rem; }

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0; }

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%; }

/*--------------------------------------------------------------
# Page Templates
--------------------------------------------------------------*/
/* General */
* {
  font-family: "Exo", sans-serif;
  color: #fff; }

.site {
  display: flex;
  flex-direction: column;
  padding: 0.75rem;
  min-height: 100vh; }
  @media only screen and (min-width: 620px) {
    .site {
      padding: 1.5rem; } }
  .site-header {
    flex: none;
    margin: 3rem 0 1.5rem 0; }
    .site-header .site-logo {
      width: 80%; }
      @media only screen and (min-width: 620px) {
        .site-header .site-logo {
          width: 32rem; } }
  .site-content {
    flex: none; }
  .site-footer {
    flex: 1 1 auto;
    margin-top: 1.5rem;
    padding: 1.5rem 0;
    display: flex;
    flex-wrap: wrap;
    align-content: flex-end; }
    @media only screen and (min-width: 620px) {
      .site-footer {
        flex: 0 1  auto;
        margin-top: 4.5rem;
        align-content: flex-start; } }

/* All Archives Page */
.archive .site-main > * {
  padding-bottom: 1.5rem;
  border-bottom: 1px dotted #fff; }
  .archive .site-main > *:last-child {
    border-bottom: none; }

.search .page-header {
  border-bottom: 1px dotted #fff; }

/* Submit a Quote Page */
.page-template-page-submit .quote-submission-wrapper form > div {
  margin-bottom: 1.5rem; }
  .page-template-page-submit .quote-submission-wrapper form > div input, .page-template-page-submit .quote-submission-wrapper form > div textarea {
    margin-top: 0.5rem; }
